/* stylelint-disable no-invalid-position-at-import-rule */
@use "sass:math";

@import "./variables/variables";
@import "./variables/colors";
@import "./mixins";
@import "./components";

// Шрифты
@include font('Inter', normal, 400, '../resources/fonts/inter/inter-regular');
@include font('Inter', normal, 500, '../resources/fonts/inter/inter-medium');
@include font('Inter', normal, 700, '../resources/fonts/inter/inter-bold');


@include font('Craftwork', normal, 400, '../resources/fonts/craftworkgrotesk/craftworkgrotesk-regular');
@include font('Craftwork', normal, 500, '../resources/fonts/craftworkgrotesk/craftworkgrotesk-medium');
@include font('Craftwork', normal, 700, '../resources/fonts/craftworkgrotesk/craftworkgrotesk-bold');

@include font('Gilroy', normal, 500, '../resources/fonts/gilroy/gilroy-medium');
// @import url('https://fonts.googleapis.com/css2?family=Inter:wght@300..900&display=swap');

// Сброс CSS
@import "./reset";
@import "./normalize";

html {
  font-size: calc(0.342466 * var(--base-size)); //10px for 1920×1000 
  // @media (max-width: 1600px) {
  //   font-size: calc(0.343466 * var(--base-size)); //10px for 1600×900 
  // }
  // @media (max-width: 1440px) {
  //   font-size: calc(0.44643 * var(--base-size)); //10px for 1440×800
  // }
}
body {
  color: var(--c-text-regular);
  font-weight: 400;
  font-family: var(--ff-regular);
  font-size: 1.6rem;
  line-height: 1.2;
  letter-spacing: -0.03em;
}
body._lock {
  overflow: hidden;
  touch-action: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  // overflow: hidden;
}
.wrapper > .content {
  position: relative;
  z-index: 1;
  flex: 1 0 auto;
}
.container {
  max-width: 159rem;
  margin-left: auto;
  margin-right: auto;
}

