.modal__content {
	max-width: 50rem;
	padding-top: 4.6rem;
	padding-bottom: 4.6rem;
	padding-left: 9rem;
	padding-right: 9rem;
	background: var(--bg-app);
	box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.25);
	border-radius: 1.7rem;
	color: #000;
	display: inline-flex;
	flex-direction: column;
}

.modal__icon {
	width: 9.6rem;
	height: 9.6rem;
	margin-bottom: 2rem;
	margin-left: auto;
	margin-right: auto;
}

.modal__title  {
	font-weight: 700;
	font-size: 3rem;
	line-height: 1.2;
	font-family: var(--ff-title);
	margin-bottom: 1.5rem;
	text-align: center;
}
.modal__desc  {
	font-size: 1.8rem;
	line-height: 1.3;
	margin-bottom: 3.5rem;
	text-align: center;
}


.modal__button {
	max-width: 100%;
	width: 100%;
	border-radius: 0.6rem;
}


.modal__content .checkbox__link {
	--link-color: var(--c-text-regular);
	// --link-color: var(--c-text-regular);
}